

$acentColor: #FC9B09;
$primaryColor: #FFF;
$secondaryColor: #C7C7C7;
$textColor: #37373F;
$textColorLight: #B3B3B3;
$linkColor: #5286EC;




%container{
    padding: 0 15px;
        @media screen and (min-width: 575px) {
            padding: 0 55px;
        }
        @media screen and (min-width: 768px) {
            padding: 0 50px;
        }
        @media screen and (min-width: 1280px) {
            padding: 0;
            width: 1140px;
            margin: 0 auto;
        }
}